import SingletonBase from "../Singleton/SingletonBase";

export default class lottery_lotteryData extends SingletonBase {
    //ID
    userId = null;
    //
    userName = null;
    //
    roleList = null;
    //code
    lotteryCode = null;
    //
    lotteryId = 1115;
    //
    awardTime = 0;
    //
    awardResult = [1, 2, 3];

    static _onNewObject() {
        let one = new lottery_lotteryData();
        return one;
    }

    updataData(data) {
        for (let one in data) {
            if (this.hasOwnProperty(one))
                this[one] = data[one];
        }
    }
}