import videoShader1 from "./videoShader1";
const { ccclass, property } = cc._decorator;
@ccclass
export default class lottery_videoCom extends cc.Component {
    //
    @property(cc.String)
    currentUrl = 'ws://ws4.jiasula.info:8081/lobbyB/B15/19148936/6eca0113e34c41c1f45a5f9eaf13429c'
    @property(cc.Sprite)
    targetSprite = null;
    @property(cc.Boolean)
    isPlay = false; //,
    //socket
    _socketController = null;

    onLoad() {
        cc.game.on(cc.game.EVENT_HIDE, this.gameHideClose, this);
        cc.game.on(cc.game.EVENT_SHOW, this.gameShowReOpen, this);
    }

    init(url, isPlay = false) {
        this.isPlay = isPlay;
        this.currentUrl = url || this.currentUrl;
        this.VideoShader = new videoShader1();
        this.VideoShader.node = this.node;
        let renderer, useWorker = false, self = this; //baccarat.USE_WORKER;
        if (cc._renderType !== cc.game.RENDER_TYPE_CANVAS) {
            this._socketController = new ebet.baccarat.VideoSocketController(this.currentUrl, useWorker, true);
        } else {
            console.log('canvas');
            return;
        }
        let shaderStatus = false;
        let texureImag = new cc.Texture2D();
        let orgHeight = this.node.height;
        let orgWidth = this.node.width;
        !this.targetSprite && (this.targetSprite = self.node.getComponent(cc.Sprite));
        this.targetSprite.spriteFrame = new cc.SpriteFrame(texureImag);
        this.scheduleOnce(() => {
            this._socketController.onPictureDecoded = function (data, pixelFormat, pixelsWidth, pixelsHeight, contentSize) {
                if (!self.isPlay) return;
                if (!shaderStatus) {
                    self.VideoShader.ShaderEffect();
                    shaderStatus = true;
                }
                self.VideoShader._currentBuffer = data;
                texureImag.initWithData(data, pixelFormat, pixelsWidth, pixelsHeight, contentSize);
                self.node.width = orgWidth;
                self.node.height = orgHeight;
            }
        }, 0)
    }
    //
    pause() {
        this.isPlay = false;
        this.gameHideClose();
    }
    //
    continue() {
        this.isPlay = true;
        this.gameShowReOpen();
    }

    gameHideClose() {
        if (this._socketController) {
            this._socketController.close();
        }
    }

    gameShowReOpen() {
        if (this._socketController && this.isPlay) {
            this._socketController._currentUrl = this.currentUrl;
            this._socketController.reOpen();
        }
    }

    update(dt) {
        if (!this.isPlay) return;
        if (this._socketController && this._socketController.onRenderingBefore) {
            this._socketController.onRenderingBefore(dt);
            this.VideoShader.Myrendering(ebet.videoSize);
        }
    }

    onDestroy() {
        cc.game.off(cc.game.EVENT_HIDE, this.gameHideClose);
        cc.game.off(cc.game.EVENT_SHOW, this.gameShowReOpen);
        this._socketController.close();
        this._socketController = null;
        this.unscheduleAllCallbacks();
    }
}
