import videosMsgFatory from "./videoMsgFactory";
import Utils from "../../Core/Tools/lottery_Utils";
import lottery_animCom from "../animCom/lottery_animCom";
import lottery_lotteryData from "../../Data/lottery_lotteryData";
import { setInterval, clearInterval } from "timers";
import lottery_videoCom from "../videoCom/lottery_videoCom";
const { ccclass, property } = cc._decorator;
//,
// 
@ccclass
export default class lottery_videoFlow extends cc.Component {
    //
    msgFuncDefine = ['firstStage', 'secondStage', 'thirdStage', 'fourStage', 'fiveStage', 'sixStage', 'waitStage'];
    //
    stanardFlow = [[7, 10], [1], [2], [3], [4], [5], [6, 7]];
    @property([cc.VideoPlayer])
    videoArr = []; //
    @property([cc.Node])
    qiShuArr = [];
    @property(cc.Node)
    nodeItem = null;
    @property(lottery_animCom)
    littleCom = null;
    @property(cc.Node)
    startLottery = null;
    @property([cc.Node])
    balls = [];
    @property(lottery_videoCom)
    videoStream = null;
    @property([cc.Sprite])
    countDownArr = [];

    //
    msgList = [];
    //
    totalTime = 60;
    //
    nowMsgPos = 0;
    //
    isPlayCountDown = false;
    //
    lotteryId;

    start() {
        for (let one of this.videoArr) {
            one.node.on('completed', this.animFinshCallBack.bind(this));
            one.node.active = false;
        }
        let arr = document.getElementsByClassName('cocosVideo');
        for (let one of arr) {
            one.muted = true;
            one.style.zIndex = '-1';
        }
        this.videoStream.node.opacity = 0;
        this.videoStream.init(null, true);
    }

    /**
     * 
     */
    startPlay() {
        clearTimeout();
        this.lotteryId = lottery_lotteryData.getInstance().lotteryId++;
        this.setQiShu(this.lotteryId, this.qiShuArr[0], true);
        this.setQiShu(this.lotteryId, this.qiShuArr[1], false)
        this.setQiShu(this.lotteryId + 1, this.qiShuArr[2], true);
        this.msgList = videosMsgFatory.anlyArr(this.stanardFlow);
        this.animFinshCallBack();
    }

    /**
     * 
     */
    stopPlay() {
        clearTimeout();
        this.videoStream.pause();
    }
    /*----------------------------------------------------------------------------------------- */
    //,
    animFinshCallBack() {
        if (this.isPlayCountDown) {
            this.videoArr[5].play();
            return;
        }
        console.log("");
        let obj = this.msgList.shift();
        if (obj) { //
            this[this.msgFuncDefine[obj.type - 1]](obj.data);
        } else {
            console.log('');
            //
            this.startPlay();
        }
    }
    //
    firstStage(data) {
        this.videoArr[0].node.active = true;
        this.videoArr[0].play();
        this.qiShuArr[0].opacity = 0;
        this.qiShuArr[0].active = true;
        let one = cc.sequence(cc.delayTime(13), cc.fadeIn(1.0), cc.delayTime(1.5), cc.fadeOut(1.0));
        this.qiShuArr[0].runAction(one);
    }
    /**
     * 
     * @param  index  
     */
    secondStage(data = 10) {
        this.videoArr[0].node.active = false;
        this.videoArr[1].node.active = true;
        this.videoArr[1].currentTime = data >= 10 ? -1 : 10 - data;
        this.videoArr[1].play();
    }
    /**
     *  
     */
    thirdStage(data) {
        for (let one of this.balls)
            one.active = true;
        this.videoStream.node.opacity = 255;
        this.videoArr[1].node.active = false;
        this.littleCom.node.active = true;
        this.startLottery.active = true;
        this.startLottery.setPosition(1145, 540);
        this.qiShuArr[1].active = true;
        this.qiShuArr[1].setPosition(1140, 294);
        this.littleCom.setData('startLotteryBg');
        this.balls[0].setPosition(1145, 33);
        this.balls[1].setPosition(1145, -193);
        this.balls[2].setPosition(1145, -410);
        this.littleCom.setFinshCallBack(() => {
            this.startLottery.runAction(cc.moveTo(0.5, cc.v2(780, 540)));
            this.qiShuArr[1].runAction(cc.moveTo(0.5, cc.v2(780, 294)));
            this.littleCom.setData('startLotteryBgLoop', 0, true);
            setTimeout(() => {
                Utils.getInstance().setSprite(this.balls[0].getComponent(cc.Sprite), 'cq_' + lottery_lotteryData.getInstance().awardResult[0], 'LEDAtlas');
                this.balls[0].runAction(cc.moveTo(1.0, cc.v2(780, 39)));
                setTimeout(() => {
                    Utils.getInstance().setSprite(this.balls[1].getComponent(cc.Sprite), 'cq_' + lottery_lotteryData.getInstance().awardResult[1], 'LEDAtlas');
                    this.balls[1].runAction(cc.moveTo(1.0, cc.v2(780, -169)));
                    setTimeout(() => {
                        Utils.getInstance().setSprite(this.balls[2].getComponent(cc.Sprite), 'cq_' + lottery_lotteryData.getInstance().awardResult[2], 'LEDAtlas');
                        this.balls[2].runAction(cc.moveTo(1.0, cc.v2(780, -381)));
                    }, 1000);
                }, 1000);
            }, 1000);
        })
        setTimeout(() => {
            this.littleCom.node.active = false;
            this.startLottery.active = false;
            this.animFinshCallBack();
        }, data || 9000);
    }
    /**
     * 
     */
    fourStage(data) {
        this.videoStream.node.opacity = 0;
        this.videoArr[2].node.active = true;
        this.videoArr[2].play();
        this.balls[0].runAction(cc.moveTo(1.0, cc.v2(-282, -267)));
        this.balls[1].runAction(cc.moveTo(1.0, cc.v2(25, -267)));
        this.balls[2].runAction(cc.moveTo(1.0, cc.v2(335, -267)));
        this.qiShuArr[1].y = 285;
        this.qiShuArr[1].runAction(cc.spawn(cc.moveTo(0.7, cc.v2(10, 0)), cc.scaleTo(0.7, 1.5, 1.5)));
    }
    /**
     *  ebet3d logo
     */
    fiveStage(data) {
        for (let one of this.balls)
            one.active = false;
        this.qiShuArr[1].active = false;
        this.qiShuArr[1].scaleX = 1;
        this.qiShuArr[1].scaleY = 1;
        this.videoArr[2].node.active = false;
        this.videoArr[3].node.active = true;
        this.videoArr[3].play();
    }
    /**
     * 
     * @param  time 
     */
    sixStage(data) {
        this.videoArr[3].node.active = false;
        this.videoArr[4].node.active = true;
        this.videoArr[4].play();
        this.qiShuArr[2].active = true;
        this.qiShuArr[2].opacity = 0;
        let one = cc.sequence(cc.delayTime(1), cc.fadeIn(1.0), cc.delayTime(7), cc.fadeOut(1.0));
        this.qiShuArr[2].runAction(one);
    }
    /**
     * ,
     * time 
     */
    waitStage(time) {
        this.isPlayCountDown = true;
        for (let one of this.videoArr)
            one.node.active = false;
        this.videoArr[5].node.active = true;
        this.videoArr[5].play();
        this.countDownArr[0].node.parent.active = true;
        this.setCountDown(time--);
        let timer1 = setInterval(() => {
            if (time < 0) {
                clearInterval(timer1);
                this.countDownArr[0].node.parent.active = false;
                this.videoArr[5].node.active = false;
                this.isPlayCountDown = false;
                this.animFinshCallBack();
            }
            this.setCountDown(time--);
            this.countDownArr[4].node.active = !this.countDownArr[4].node.active;
        }, 1000);
    }
    /*--------------------------------------------------------------------------------------------*/
    /**
     * 
    */
    setQiShu(str, node, isBig = true) {
        node.removeAllChildren();
        let arr = (str + '').split('');
        let func = (url) => {
            let one = cc.instantiate(this.nodeItem);
            Utils.getInstance().setSprite(one.getComponent(cc.Sprite), url, 'LEDAtlas');
            node.addChild(one);
        }

        if (isBig) {
            func('wz_di');
            for (let name of arr) {
                func('wz_sz_' + name);
            }
            func('wz_qi');
        } else {
            func('wz_di_2');
            for (let name of arr) {
                func('wz_xsz_' + name);
            }
            func('wz_qi_2');
        }

    }
    /**
     * ,
     */
    setCountDown(time = 60) {
        let minute = Math.floor(time / 60);
        let second = time - minute * 60;
        minute = (minute + '').split('');
        second = (second + '').split('');
        if (minute.length == 1) minute.unshift(0);
        if (second.length == 1) second.unshift(0);
        Utils.getInstance().setSprite(this.countDownArr[0], minute[0], 'countDown');
        Utils.getInstance().setSprite(this.countDownArr[1], minute[1], 'countDown');
        Utils.getInstance().setSprite(this.countDownArr[2], second[0], 'countDown');
        Utils.getInstance().setSprite(this.countDownArr[3], second[1], 'countDown');
    }
}
//